import React, { useEffect, useState } from 'react';
import './trade.less'
import TradeInfo from './TradeInfo/TradeInfo'
import { getCookie } from '../../utils/utils'
import Tradingview from './Tradingview/Tradingview'
import NetWorkHint from '../../components/NetWorkHint/NetWorkHint';
import { inject, observer } from 'mobx-react';
import { DeriEnv } from '../../lib/web3js';
import MyInfo from './MyInfo/MyInfo';
import ContractInfo from './ContractInfo/ContractInfo';
import AccountInfo from './Account/AccountInfo';
import OrderBar from './Sidebar/OrderBar';
import classNames from 'classnames';
import SymbolInfo from './Sidebar/SymbolArea/SymbolInfo';
import { useRouteMatch } from 'react-router';
import { useParams } from 'react-router-dom'
import Category from '../../model/Category';
import Type from '../../model/Type';
import TradeGuide from '../../components/TradeGuide/TradeGuide';
import AnnouncementVip from '../../components/Announcement/AnnouncementVip';
import {useWallet} from 'use-wallet'
import Announcement from '../../components/Announcement/Announcement';
import AnnouncementClose from '../../components/Announcement/AnnouncementClose';
const Env = DeriEnv.get()

function Trade({ trading, loading, wallet, version, lang, getLang }) {
  const [loaded, setloaded] = useState(false)
  const [isMobileLayout, setIsMobileLayout] = useState(false)
  const [isShowHint, setIsShowHint] = useState(false)
  const [isNewAccount, setIsNewAccount] = useState(false)
  const isFutrueRetired = useRouteMatch({ path: '/retired/trade/futures' })
  const isOptionRetired = useRouteMatch({ path: '/retired/trade/options' })
  const isFutrue = useRouteMatch('/trade/futures')
  const walletContext = useWallet();

  const { symbol, baseToken } = useParams()

  const layoutClass = classNames('trade-layout', {
    'mobile-trade-layout': isMobileLayout
  })

  const orderBarClass = classNames('orderbar', {
    retired: trading.pool && trading.pool.retired
  })

  useEffect(() => {
    loading.loading();
    wallet.setStatus(walletContext.status)
    trading.init(wallet, () => {
      setloaded(true)
    }, isFutrueRetired || isOptionRetired, symbol, baseToken)
    return () => trading.clean()
  }, [wallet.detail.account, wallet.detail.chainId, version.current, Type.current, Category.current, symbol, baseToken])
  

  return (
    <div>
      <TradeGuide lang={lang} trading={trading} wallet={wallet} getLang={getLang} isNewAccount={isNewAccount} />
      <div className='network-hint'><NetWorkHint wallet={wallet}/></div>
      <AnnouncementVip wallet={wallet} />
      <AnnouncementClose wallet={wallet} />
      <div className={layoutClass}>
        <div className='symbol-area'><SymbolInfo lang={lang} onGirdLayoutChange={(isMobileLayout) => setIsMobileLayout(isMobileLayout)} /></div>
        <div className='account-info'><AccountInfo lang={lang} getLang={getLang} /></div>
        <div className={orderBarClass}><OrderBar lang={lang} /></div>
        <div className='trade-info'><TradeInfo lang={lang} /></div>
        <div className='kline'><Tradingview lang={lang} /></div>
        <div className='my-info'><MyInfo lang={lang} getLang={getLang} /></div>
        <div className='contract'><ContractInfo lang={lang} getLang={getLang} /></div>
        {/* <div className='trades'><Trades lang={lang}/></div> */}
      </div>
    </div>

  )
}

export default inject('wallet', 'type', 'version', 'loading', 'trading')(observer(Trade))
